import React from "react";
import styles from "./item.module.css";
import Icon from "icon/createIcon";

export default ({ title, participantAvatar, desc, participantNum }) => (
  <div className={`${styles.frame} swiper-slide`}>
    <div className={styles.typeWrapper}>
      <div className={styles.typeName}>今日话题</div>
    </div>
    <div className={styles.title}>{title}</div>
    <div className={styles.desc}>{desc}</div>
    <div className={styles.joinList}>
      {participantAvatar
        ? participantAvatar.map((url, index) => (
            <React.Fragment key={index}>
              <img
                alt={""}
                src={
                  url
                    ? url
                    : "http://nos.netease.com/yanxuan/8945ae63d940cc42406c3f67019c5cb6.png?imageView&thumbnail=48y48"
                }
                className={styles.photo}
              />
            </React.Fragment>
          ))
        : ""}
      <div className={styles.joinIcon}>
        <Icon name="joinIn" />
      </div>
      {participantAvatar ? `${participantNum}人参与话题` : ""}
    </div>
  </div>
);



