import React from "react";
import styles from "./item.module.css";

export default ({ articleCount, title, picUrl }) => (
  <div className={`${styles.wrapper} swiper-slide`}>
    <div className={styles.photoWrapper}>
      <img src={picUrl} className={styles.photo} alt={""} />
      <div className={styles.number}>{articleCount}</div>
    </div>
    <div className={styles.title}>{title}</div>
  </div>
);



