import React from "react";
import styles from "./lock.module.css";

export default ({ avatar, mediaList, content, nickname, picUrl }) =>
  mediaList[0].pic !== null ? (
    <div className={styles.frame}>
      <div className={styles.header}>严选LOOK</div>
      <img src={mediaList[0].pic.url} alt="" className={styles.photo} />
      <div className={styles.author}>
        <img src={avatar} alt="" className={styles.headerPlaceholder} />
        {nickname}
      </div>
      <div className={styles.title}>{content}</div>
    </div>
  ) : null;



