import { Subject, from } from "rxjs";
import { switchMap, filter } from "rxjs/operators";
export default (listUrl, page, size) => {
  let pageIndex = page;
  const ajaxTransfer = function(page) {
    const getAjax = new XMLHttpRequest();
    return new Promise(function(resolve, reject) {
      getAjax.onreadystatechange = function() {
        if (getAjax.readyState === 4) {
          if (getAjax.status === 200) {
            const jsonData = JSON.parse(getAjax.response).data;
            const listData = {
              findMore: [],
              hasMore: jsonData.hasMore
            };
            jsonData.findMore.map(
              value => (listData.findMore[value.topicId] = value)
            );
            pageIndex += 1;
            resolve(listData);
          } else {
            reject(getAjax.status);
          }
        }
      };
      getAjax.open("GET", `${listUrl}?page=${page}&size=${size}`);
      getAjax.send();
    });
  };
  const putState = new Subject();
  const outState = putState.pipe(
    filter(({ type }) => type === "GET_cache"),
    switchMap(() => from(ajaxTransfer(pageIndex)))
  );

  return { putState, outState };
};



