import { Carousel, WingBlank } from "antd-mobile";
import React from "react";
import styles from "./swipePhoto.module.css";

export default class SwipePhoto extends React.Component {
  constructor() {
    super(...arguments);
    this.state = { list: this.props.list };
  }
  render() {
    return (
      <WingBlank>
        <Carousel
          className={styles.spaceCarousel}
          frameOverflow="visible"
          cellSpacing={10}
          slideWidth="335px"
          dots={false}
          infinite={true}
        >
          {this.state.list.map(({ picUrl, subTitle, title, desc }) => (
            <div key={title}>
              <img src={picUrl} alt="" className={styles.photo} />
              <div className={styles.wrapper}>
                <div className={styles.toBeCenter}>
                  <div className={styles.subtitle}>{subTitle}</div>
                </div>
                <div className={styles.title}>{title}</div>
                <div className={styles.desc}>{desc}</div>
              </div>
            </div>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}



