import React from "react";
import WrappedComponent from "animationLoad/wrapper";
import Header from "header/header";
import SwipePhoto from "./swipePhoto/swipePhoto";
import PaperList from "./paperList/list";
import RecommendList from "./recommendList/index";
import SplendidMoment from "./splendidMoment/splendidMoment";
import Lock from "./lock/lock";
import ExploreMore from "./exploreMore/exploreMore";
import ReturnTop from "returnTop/index";

class DiscernList extends React.Component {

  render() {
      document.title='识物';
    const {
      banner,
      column,
      recommendOne,
      recommendTwo,
      recommendThree,
      zhenOne,
      zhenTwo,
      zhenThree,
      tenfifteen,
      yxLook
    } = this.props;
    return (
      <div>
        <ReturnTop />
        <Header />
        <SwipePhoto list={banner} />
        <PaperList list={column} />
        <RecommendList
          header="为你推荐"
          banner={recommendOne}
          listState={[recommendTwo, recommendThree]}
        />
        <SplendidMoment list={tenfifteen} />
        <RecommendList
          header="严选甄品"
          banner={zhenOne}
          listState={[zhenTwo, zhenThree]}
        />
        <Lock {...yxLook} />
        <ExploreMore />
      </div>
    );
  }
}

export default WrappedComponent(DiscernList);



