import React, { Component } from "react";
import { ListView } from "antd-mobile";
import styles from "./exploreMore.module.css";
import Item from "./item/item";
import Observable from "./model/scrollCache";

export default class ExploreMore extends Component {
  constructor() {
    super(...arguments);
    const getObservable = Observable("/hot_list", 1, 5);
    this.listdata = {};
    const dataSource = new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2
    });
    this.state = {
      dataSource,
      hasMore: true
    };
    this.outState = getObservable.outState;
    this.putState = getObservable.putState;
    this.onEndReached = this.onEndReached.bind(this);
  }
  componentDidMount() {
      this.outState = this.outState.subscribe({
      next: value => {
        value.findMore.map(value => (this.listdata[value.topicId] = value));
        this.setState({
          dataSource: this.state.dataSource.cloneWithRows(this.listdata),
          hasMore: value.hasMore
        });
      }
    });
  }
  componentWillUnmount() {
      this.outState.unsubscribe();
      this.putState.unsubscribe();
  }
  onEndReached = () => {
    if (!this.state.hasMore) {
      return false;
    }
    this.putState.next({
      type: "GET_cache"
    });
  };
  render() {
    return (
      <div className={styles.list}>
        <div className={styles.headerWrapper}>
          <div className={styles.header}>更多精彩</div>
        </div>
        <ListView
          dataSource={this.state.dataSource}
          renderRow={Item}
          scrollRenderAheadDistance={500}
          onEndReached={this.onEndReached}
          onEndReachedThreshold={500}
          useBodyScroll
        />
      </div>
    );
  }
}



