import React from "react";
import styles from "./item.module.css";

export default data => {
  let { picList, subtitle, itemPicUrl, topicId } = data;
  return (
    <div className={styles.wrapper} key={topicId}>
      {picList === null ? (
        <img src={itemPicUrl} className={styles.uniqueness} alt={""} />
      ) : (
        ""
      )}
      {picList !== null ? (
        <div className={styles.photoList}>
          <img src={picList[0]} className={styles.leftList} alt={" "} />
          <div className={styles.rightList}>
            <div
              style={{
                backgroundImage: `url(${picList[1]})`,
                width: "100%",
                height: "100%",
                backgroundSize: "100% 100%"
              }}
            />
            <div
              style={{
                backgroundImage: `url(${picList[2]})`,
                width: "100%",
                height: "100%",
                backgroundSize: "100% 100%"
              }}
            />
          </div>
        </div>
      ) : (
        ""
      )}
      <div className={styles.title}>{subtitle}</div>
    </div>
  );
};



